/*
 * Header of section home pages
 */

 @use "../abstracts" as *;

.page-header {
	z-index: 1;
	color: white;
	padding: 30px;
	margin-bottom: 30px;
	position: relative;
	background-image: linear-gradient(rgba(0,0,0,0.8),rgba(0,0,0,0.8)), url('/static/images/gallery/img.jpg');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 32%;

	@include min-responsify($device-small) {
		margin-bottom: 50px;
	}
    
    h1 {
	    margin: 0;
	    text-align: center;
	    font-family: 'Inter-thin';
    }

	&#contact-header {
		z-index: 1;
		padding: 50px 0px 60px 0px;
		text-align: center;
		position: relative;
		background-image: linear-gradient(rgba(0,0,0,0.8),rgba(0,0,0,0.8)), url("/static/images/gallery/img12.jpg");
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;

		> div:first-of-type {
			position: relative;
			animation: slide_in 0.5s;

			h2 {
				margin-bottom: 0.5em;
			}

			p {
				width: 100%;
				font-size: 1em;
				margin: auto;
			}
		}

		@include max-responsify($device-small) {
			p {
				font-size: 0.9em;
			}
		}
	}
}